import "./Nome.css";

const Nome = ( { dado, updateCampo } ) => {
    return(
        <div>
            <div className="textao">
                <p style={{fontSize:"1.8vh"}}>
                No dia 29/11/2024, foi iniciada a <strong>Turn Especial de Natal</strong> do Jo.
                <br/><br/>
                Ela contou com 12 datas no total e levou a magia natalina por nove estados do Brasil.
                <br/><br/>
                Fs de todo o pas entraram no clima natalino junto com a turn: fantasias, previses de <i>cover</i> e projetos de f para f marcaram a poca.
                <br/><br/>
                Em comemorao a uma turn to... <i>especial</i>, criei esse site para que <strong>voc</strong> possa relembrar e escolher seus momentos favoritos dos shows.
                <br/><br/>
                Mas, antes de continuarmos,
                </p>
            </div>
            <h1>Qual  o seu nome?</h1>
        <input type="text" maxLength={20} className="escreva" value={dado.nome} onChange={(e) => updateCampo("nome", e.target.value)} placeholder="Escreva seu nome ou @..."></input></div>
    )
}

export default Nome;